.footer-navigation-container {
  width: 320px;
  box-sizing: border-box;
  background-color: #f2f2f2;
  padding: 40px 20px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
}

.footer-navigation-logo__text--icon {
  content: "";
  width: 128px;
  height: 24px;
  mask-image: url("../img/sprite.svg#logo__text");
  background-size: cover;
  background-color: $base-text-color;

  &:hover {
    background-color: #3e3e3e;
  }

  &:active {
    background-color: #6b6b6b;
  }
}


.footer-navigation__copyright {
  width: 280px;
}

.footer-navigation__copyright-logo {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.footer-navigation__copyright--icon {
  content: "";
  width: 27px;
  height: 34px;
  mask-image: url("../img/sprite.svg#copyright-icon");
  background-size: cover;
  background-color: #666666;

  &:focus,
  &:hover {
    background-color: #68b738;
  }

  &:active svg {
    background-color: rgba(104, 183, 56, 0.3);
  }
}

.footer-navigation__copyright-logo:active span {
  color: rgba(68, 68, 68, 0.3);
}

.footer-navigation__copyright-name {
  display: block;
  color: $addithional-text-color;
  font-size: 16px;
  font-family: $Lato;
  font-weight: 400;
  line-height: 20px;
  align-self: center;
}

.footer-navigation__social {
  position: relative;

  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 280px;
    height: 1px;
    background-color: #d9d9d9;
  }

  &::before {
    top: -20px;
  }

  &::after {
    top: 42px;
  }
}

.footer-navigation__copyright-icon {
  fill: #666666;
}

@media (min-width: $tablet-width) {
  .footer-navigation-container {
    width: 768px;
    padding: 55px 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 0;
  }

  .footer-navigation__copyright {
    width: 168px;
  }

  .footer-navigation__copyright-logo {
    justify-content: end;
    gap: 23px;
  }

  .footer-navigation__social::before,
  .footer-navigation__social::after {
    display: none;
  }
}

@media (min-width: $desktop-width) {
  .footer-navigation-container {
    width: 1440px;
    padding: 66px 110px;
  }
}
